import React, { Component } from "react";
import { Row, Col, Clearfix, Image } from "react-bootstrap";
import axios from "axios";
import Gallery from 'react-grid-gallery';
// import { InstagramPosts } from "instagram-screen-scrape"
import { RingLoader } from "react-spinners";
import { css } from "@emotion/core"
class Pic extends Component {

    constructor(props) {
        super(props)
        this.state = {
            account: this.props.account,
            data: {},
            loading: "true",
            scraped: "false"
        }
        // this.componentDidMount.scraper = this.componentDidMount.scraper.bind(this)
    }

    componentDidMount() {
        console.log("PicData", this.props);
        //IDEA
        //Maybe accomplish the same thing with a fade in
        // component mounts
        // wait .5 seconds, if request not completed, then start loader
        //

        // if (this.state.scraped === "false") {
        //     const scraper = async () => {

        //         let Stream = await new InstagramPosts({
        //             username: "ameliarempe"
        //         })
        //         Stream.on("data", (post) => {
        //             console.log(post)
        //         })
        //     }
        //     scraper()
        //     // .then((newStream) => {
        //     //     console.log("Scraped Data: ", newStream);
        //     //     newStream.on('data', (post) => {
        //     //         console.log("POSTTTTT", post)
        //     //     })
        //     //     this.setState({
        //     //         scraped: "true"
        //     //     })
        //     // });
        // }


        //INSTAGRAM API:
        axios.get("https://api.instagram.com/v1/users/self/media/recent/?access_token=349225995.27b1b5b.defe388f8fd04775b2bc9f711eb35f3e")
            .then(response => {
                console.log("DATA1", response.data.data);
                this.setState({
                    data: response.data.data
                })
            })
            .then(() =>
                setTimeout(() =>
                    this.setState({
                        loading: "false"
                    }, () => console.log("false")), 1500
                )
            )
            .catch(function (error) {
                console.log(error);
                this.setState({
                    loading: "error"
                })
            });
    }

    render() {
        if (this.state.loading === "true") {
            
            const override = css`
            display: block;
            position: relative;
            margin: 0 auto;
            border-color: #8CA0AB;
            border-thickness: 5px;
            display: block; 
	        vertical-align: middle; 
	        white-space: normal;
            `;

            return (
                <Col className="loading">
                    <span classname="ltContainer">
                        <h1 className="loadingText">Loading</h1>
                    </span>
                    <RingLoader
                        css={override}
                        loading={true}
                        size={150}
                        color={'#C2887D'}
                        // sizeUnit={"%"}
                    />
                </Col>
            )
        } else if (this.state.loading === "false") {
            const data = this.state.data
            const height = 400
            //Insta API
            const DATA = data.map(item => ({
                src: item.images.standard_resolution.url,
                thumbnail: item.images.standard_resolution.url,
                thumbnailWidth: height,
                thumbnailHeight: height * (item.images.standard_resolution.height / item.images.standard_resolution.width),
                caption: item.caption.text,
                alt: ""
            }))
            console.log("DATAZ", DATA)
            return (
                <Gallery
                    images={DATA}
                    enableImageSelection={false}
                />
            )

        } else {
            return (
                <Col>
                    <div className="errorGram">
                        There was an error loading Instagram posts
                    </div>
                </Col>
            )
        }

        // if (this.state.data) {
        //     return (
        //         this.state.data.map()
        //     )
        // }
    }


};

export default Pic;